import { Component, OnInit, HostListener, ElementRef } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { Config } from '../config.service';
import { trigger, state, style, animate, transition, group } from '@angular/animations';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
  animations: [
    trigger('fadeInOut', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter', animate('800ms ease-out')),
      transition(':leave', animate('800ms ease-in')),
    ])
  ]
})
export class ProductComponent implements OnInit {

  chart: Chart;
  scrolledTo: boolean;
  showAI : boolean;
  showQuantum : boolean;

  constructor(private charts: Config, public el: ElementRef) { }

  ngOnInit() {
    this.chart = new Chart(this.getChart());
    this.scrolledTo = false;
    this.showAI = true;
    this.showQuantum = false;
  }

  getChart() {
    return this.charts.getChart();
  }

  // onClick(value : string) {
  //   if (value == 'AI') {
  //     this.showAI = true;
  //     this.showQuantum = false;
  //   } else if (value == 'Quantum') {
  //     this.showAI = false;
  //     this.showQuantum = true;
  //   }
  // }

  @HostListener('window:scroll', ['$event'])
    checkScroll() {
      const componentPosition = this.el.nativeElement.offsetTop
      const scrollPosition = window.pageYOffset + (window.screen.availHeight/2)

      if (scrollPosition >= componentPosition) {
        this.scrolledTo = true;
        this.charts.changeNavMenuActive('Product');
      } else {
        this.scrolledTo = false;
      }

    }


}
