
import { Component, OnInit, HostListener, ElementRef } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { Config } from '../config.service';
import { trigger, state, style, animate, transition, group } from '@angular/animations';

@Component({
  selector: 'app-market',
  templateUrl: './market.component.html',
  styleUrls: ['./market.component.css'],
  animations: [
    trigger('fadeInOut', [
      state('show', style({
        opacity: 1
      })),
      state('hide',  style({
        opacity: 0
      })),
      transition('show => hide', animate('1s 2000ms ease-out')),
      transition('hide => show', animate('1s 2000ms ease-out'))
    ])
  ]
})
export class MarketComponent implements OnInit {

  chart07: Chart;
  chart17: Chart;
  firstState = 'show';
  secondState = 'hide';
  showChart07: boolean;
  scrolledTo: boolean;
  constructor(private charts: Config, public el: ElementRef) { }

  ngOnInit() {
    this.chart07 = new Chart(this.getChart2007());
    this.chart17 = new Chart(this.getChart2017());
    this.showChart07 = true;
    this.scrolledTo = false;
  }

  getChart2007() {
    return this.charts.getMarketChart2007();
  }

  getChart2017() {
    return this.charts.getMarketChart2017();
  }

  onClick() {
    this.showChart07 = !this.showChart07;
    this.firstState = 'hide';
    this.secondState = 'show';
  }

  @HostListener('window:scroll', ['$event'])
    checkScroll() {
      const componentPosition = this.el.nativeElement.offsetTop
      const scrollPosition = window.pageYOffset + (window.screen.availHeight/2)

      if (scrollPosition >= componentPosition) {
        this.scrolledTo = true;
        this.charts.changeNavMenuActive('Market');
      } else {
        this.scrolledTo = false;
      }

    }
}
